/* eslint-disable max-len */
/**
 * Makes a search request using the TomTom
 * [Search API - Points of Interest Search](POINTS_INTEREST_SEARCH_URL).
 *
 * Parameters need to be be passed to the constructor.
 * The method {{#crossLink "Services.services.poiSearch/go:method"}}{{/crossLink}} performs the actual call.
 *
 * @example
 * ```js
 * function callbackFn(result) {
 *    console.log(result);
 * };
 *
 * tt.services.poiSearch(({
 *   key: <Your API key>,
 *   query: 'pizza'
 * })
 * .go()
 * .then(callbackFn);
 * ```
 * The SDK provides a {{#crossLink "Promise"}}{{/crossLink}} polyfill for browsers (IE<9)
 * without native ECMAScript 6 support.
 *
 * @class poiSearch
 * @namespace Services.services
 * @extends Search
 * @uses ExtendedPostalCodesForMixin
 * @uses CenterMixin
 * @uses BoundingBoxMixin
 * @uses CountrySetMixin
 * @uses OffsetMixin
 * @uses BestResultMixin
 * @uses QueryMixin
 * @uses TypeaheadMixin
 * @uses ViewMixin
 * @constructor
 * @module Services
 *
 * @param {Object} [options] Options to be passed to the search call,
 *     or an array of such options objects to perform a batch request.
 */

/**
 * This option specifies the search radius in meters using the coordinates given to the `center`
 * option as origin.
 *
 * @attribute radius
 * @param {Number} [options.radius=0] A positive integer value in meters.
 */

/* eslint-enable */
import search from './search';
import { boundingBox } from './searchParameters';

const SearchPrototype = search.prototype.constructor;

function PoiSearch(options) {
    SearchPrototype.call(this, options);

    this.fields.type = {
        defaultValue: this.TYPE.poi,
        visible: false
    };

    this.fields.boundingBox = boundingBox();
}

PoiSearch.prototype = new SearchPrototype();
PoiSearch.prototype.constructor = PoiSearch;

export const poiSearch = (options) => {
    return (new PoiSearch(options)).construct();
};




