/**
 * Base class for all the Search services.
 *
 * @private
 * @class Search
 * @module Services
 * @uses GoMixin
 * @uses ProtocolMixin
 * @uses KeyMixin
 * @uses LimitMixin
 * @uses LanguageMixin
 * @uses BatchMixin
 *
 * @constructor
 * @param {Object} [options] Options to be passed to the search call,
 *     or an array of such options objects to perform a batch request.
 */
/* jshint nomen:false */
import {SERVICE_TYPES} from 'Core/serviceTypes';
import utils from '../utils';
import { searchRest } from '../../rest/search';
import SearchModel from '../../model/search/search';
import batchModel from '../../model/search/batchModel';
import searchTypes from '../../common/searchTypes';
import searchParams from './searchParameters';
import serviceFactory from '../serviceFactory';

const TYPES = {
        search: searchTypes.search,
        poi: searchTypes.poi,
        category: searchTypes.category,
        geometry: searchTypes.geometry,
        nearby: searchTypes.nearby,
        lowBandwidth: searchTypes.lowBandwidth
    }, fields = searchParams.create();

function Search(options) {
    this.options = options;
    this.defaultFields = utils.clone(fields);
    this.fields = {};
}

Search.prototype.TYPE = TYPES;
Search.prototype.model = SearchModel;
Search.prototype.construct = function() {
    this.fields = utils.addFields.call(this, this.fields);

    const service = serviceFactory(
        this.fields,
        SERVICE_TYPES.SEARCH,
        this.handleServiceCall.bind(this),
        this.handleBatchServiceCall.bind(this)
    );
    return service(this.options);
};

Search.prototype._getRestService = function() {
    return searchRest;
};

Search.prototype.handleBatchServiceCall = function(data) {
    return this._getRestService().batch(this.fields, data).then(results => batchModel(results));
};

Search.prototype.handleServiceCall = function(options) {
    return this._getRestService().search(this.fields, options)
        .then((response) => options.bestResult ? this.model(response.results[0]) : this.model(response));
};

function searchConstructor(options) {
    return (new Search(options)).construct();
}

searchConstructor.prototype.constructor = Search;

export default searchConstructor;



