/**
 * Makes a search request using the TomTom
 * [Search API - Fuzzy Search](FUZZY_SEARCH_URL).
 *
 * Parameters need to be be passed to the constructor.
 * The method {{#crossLink "Services.services.fuzzySearch/go:method"}}{{/crossLink}} performs the actual call.
 *
 *
 * The list of all supported category codes are available
 * [here](SEARCH_API_SUPPORTED_CATEGORY_CODES_URL).
 *
 * @example
 * ```js
 * function callbackFn(result) {
 *   console.log(result);
 * };
 *
 * tt.services.fuzzySearch({
 *   key: <Your API key>,
 *   query: 'Amsterdam'
 * })
 * .go()
 * .then(callbackFn);
 * ```
 *
 * The SDK provides a {{#crossLink "Promise"}}{{/crossLink}} polyfill for browsers (IE<9)
 * without native ECMAScript 6 support.
 *
 * @class fuzzySearch
 * @namespace Services.services
 * @extends Search
 * @uses ExtendedPostalCodesForMixin
 * @uses CenterMixin
 * @uses RadiusMixin
 * @uses BoundingBoxMixin
 * @uses CountrySetMixin
 * @uses OffsetMixin
 * @uses BestResultMixin
 * @uses QueryMixin
 * @uses TypeaheadMixin
 * @uses MaxFuzzyLevel
 * @uses MinFuzzyLevel
 * @uses IdxSetMixin
 * @uses ViewMixin
 * @constructor
 * @module Services
 *
 * @param {Object} [options] Options to be passed to the search call,
 *     or an array of such options objects to perform a batch request.
 */

import validators from '../validators';
import search from './search';
import { boundingBox } from './searchParameters';

const SearchPrototype = search.prototype.constructor;

function FuzzySearch(options) {
    SearchPrototype.call(this, options);

    this.fields.minFuzzyLevel = {
        validators: [validators.fuzzinessLevel],
        defaultValue: 1
    };

    this.fields.maxFuzzyLevel = {
        validators: [validators.fuzzinessLevel],
        defaultValue: 2
    };

    this.fields.idxSet = {
        validators: [validators.string]
    };

    this.fields.boundingBox = boundingBox();
}

FuzzySearch.prototype = new SearchPrototype();
FuzzySearch.prototype.constructor = FuzzySearch;

export const fuzzySearch = (options) => {
    return (new FuzzySearch(options)).construct();
};




