/**
 * Makes a search request using the TomTom
 * [Search API - Category Search](CATEGORY_SEARCH_URL).
 *
 * Parameters need to be passed to the constructor.
 * The method {{#crossLink "Services.services.categorySearch/go:method"}}{{/crossLink}} performs the actual call.
 *
 *
 * The list of all supported category codes are available
 * [here](SEARCH_API_SUPPORTED_CATEGORY_CODES_URL).
 *
 * @example
 * ```js
 * function callbackFn(result) {
 *   console.log(result);
 * };
 *
 * tt.services.categorySearch({
 *    key: <Your API key>,
 *    query: 'restaurant'
 * })
 * .go()
 * .then(callbackFn);
 * ```
 *
 * The SDK provides a {{#crossLink "Promise"}}{{/crossLink}} polyfill for browsers (IE<9)
 * without native ECMAScript 6 support.
 *
 * @class categorySearch
 * @namespace Services.services
 * @extends Search
 * @uses ExtendedPostalCodesForMixin
 * @uses CenterMixin
 * @uses RadiusMixin
 * @uses BoundingBoxMixin
 * @uses CountrySetMixin
 * @uses OffsetMixin
 * @uses BestResultMixin
 * @uses QueryMixin
 * @uses TypeaheadMixin
 * @uses ViewMixin
 * @constructor
 * @module Services
 *
 * @param {Object} [options] Options to be passed to the search call,
 *     or an array of such options objects to perform a batch request.
 *
 */
import search from './search';
import { boundingBox } from './searchParameters';

const SearchPrototype = search.prototype.constructor;

function CategorySearch(options) {
    SearchPrototype.call(this, options);

    this.fields.type = {
        defaultValue: this.TYPE.category,
        visible: false
    };

    this.fields.boundingBox = boundingBox();
}

CategorySearch.prototype = new SearchPrototype();
CategorySearch.prototype.constructor = CategorySearch;

export const categorySearch = (options) => {
    return (new CategorySearch(options)).construct();
};




